header {
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.0rem;
    justify-content: center;
}
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: -2rem;
    top: 9.4rem;
}
.header__socials::before, .header__socials::after {
    content: '';
    width: 2px;
    height: 3rem;
    background: var(--color-light);
}

/* IMG */
.me {
    background-image: linear-gradient(45deg, var(--color-light), var(--color-white));
    height: 13rem;
    /*transform: skewY(-3deg);*/
}

.bluebar__top {
    margin-top: 2.5rem;
    background-image: linear-gradient(45deg, var(--osk-games-color-1), var(--osk-games-color-2));
    height: 1.5rem;
    /*transform: skewY(-3deg);*/
}

.me__content {
    max-width: 50em;
	margin: 0 auto;
    transform: translateY(20%);
}

/* TEXT */
.subtitle {
    font-style: italic;
    margin-top: 1rem;
    margin-bottom: 3rem;
}

.leftAligned {
    text-align: left;
    margin-top: 1rem;
    z-index: 100;
}

.images {
    position: absolute;
    image-resolution: 50%;
    height: fit-content;
}

.lista {
    margin-bottom: 6rem;
}

.overImage {
    position: absolute;
}

.horizontalLayout {
    margin-top: 2rem;
    overflow: hidden;
}

.horizontalLayoutElement {
    float: left;
    width: 33%;
}