
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;400;700&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --background-color-1: #282829;
    --background-color-2: #2a4666;
    --background-color-other: #525252;

    --osk-games-color-1: #2d64f1;
    --osk-games-color-2: #57a6ff;

    --primary-color: #b4c8f5;
    --primary-color-other: #e2efff;
    --color-white: #ffffff;
    --color-light: #e6e6f3;

    --container-width-large: 75%;
    --container-width-medium: 86%;
    --container-width-small: 90%;

    --transition: all 400ms ease;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Roboto Mono', monospace;
    background-image: linear-gradient(-45deg, var(--background-color-1), var(--background-color-2));
    color: var(--color-white);
}

/* GENERAL */
.container {
    width: var(--container-width-large);
    margin: 0 auto;
} 

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--primary-color);
    margin-bottom: 3rem;
}

.light-text {
    color: var(--primary-color-other);
}

a {
    color: var(--primary-color);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.button {
    width: max-content;
    display: inline-block;
    color: var(--primary-color);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;

    border: 1px solid var(--primary-color);
    transition: var(--transition);
}

.button:hover {
    background: var(--color-white);
    color: var(--background-color-other);
    border: var(--primary-color-other);
}

.button-primary {
    background: var(--primary-color);
    color: var(--background-color);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERIES */

/* MEDIUM SIZE SCREENS */
@media screen and (max-width: 1024px){
    .container {
        width: var(--container-width-medium);
    }

    section {
        margin-top: 6rem;
    }
}

/* SMALL SIZE SCREENS */
@media screen and (max-width: 600px){
    .container {
        width: var(--container-width-small);
    }

    section > h2 {
        margin-top: 2rem;
    }
}
